






































































































































































































  const COUNTSECEND = 60
  
	export default {
		data() {
			return {
				error: {
          type: 0,
          msg: ''
        },
        errorTimer: null,
        uploadImg: '',
        uploading: false,
        editShow: false,
        editType: null,
        realname: '',
        idcard: '',
        mobile: '',
        mobileCode: '',
        hasSendCode: false,
        isClickCode: false,
        sending: false,
        countNum: COUNTSECEND,
        countTimer: null,
        wx: '',
        password1: '',
        password2: '',
        passwordCode: '',
        hasSendCode2: false,
        isClickCode2: false,
        sending2: false,
        countNum2: COUNTSECEND,
        countTimer2: null,
        editing: false,
        pregressArr: {
          apply: 0,
          apply_toward: 0,
          banker: 0,
          banker_toward: 0,
          canLevelUp: false
        }
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      role() {
        return this.userInfo.role
      },
      hasArrow() {
        return this.role < 4 || this.userInfo.agent_type === 2
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      },
      roleTips() {
        if (this.role < 4 && this.role > 1) {
          return '立即升级'
        } else if (this.role === 4) {
          if (this.userInfo.agent_type === 2) {
            return '付费升级'
          } else {
            return '完成任务升级'
          }
        } else {
          return '升级VIP'
        }
      },
      isReal() {
        return this.userInfo.is_real === 1
      },
      popEditTitle() {
        if (this.editType === 'real') return '实名认证'
        if (this.editType === 'password') return '修改密码'
        if (this.editType === 'mobile') return '绑定手机'
        return '请填写需要修改的信息'
      },
      showForGet() {
        return (this.$store.state.loginType === this.$constant.LOGIN_TYPE_MOBILE) || (this.$store.state.loginType === this.$constant.LOGIN_TYPE_WECHAT && this.userInfo.mobile)
      },
      showBindwx() {
        return this.$store.state.loginType === this.$constant.LOGIN_TYPE_MOBILE
      }
    },
		onLoad() {
      // 升级进度
      if (this.role >= 1 && this.role <= 3) {
        this.$http({
          url: this.$apis.sxProgress,
          method: 'GET',
          data: {
            id: this.userInfo.id
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.pregressArr = data.data
            } else {
              uni.showToast({
              	title: '获取升级进度数据失败',
                icon: 'none'
              })
            }
          }
        })
      }
      // 显示实名认证弹窗
      if (uni.getStorageSync('real')) {
        this.gotoReal()
        uni.removeStorageSync('real')
      }
		},
    onShow() {
    	// 是否处于验证码获取倒计时状态
    	if (uni.getStorageSync('accountCodeStamp')) {
    	  let oldtime = uni.getStorageSync('accountCodeStamp')
    	  let nowtime = new Date().getTime()
    	  let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
    	  if (cha < COUNTSECEND) {
    	    this.sending = true
    	    this.countNum = COUNTSECEND - cha
    	    this.countTimer = setInterval(() => {
    	      this.countNum --
    	      if (this.countNum <= 0) {
    	        clearInterval(this.countTimer)
    	        this.countNum = COUNTSECEND
    	        this.sending = false
    	        this.isClickCode = false
    	      }
    	    }, 1000)
    	    this.hasSendCode = true
    	  }
    	}
      if (uni.getStorageSync('accountCodeStamp2')) {
        let oldtime = uni.getStorageSync('accountCodeStamp2')
        let nowtime = new Date().getTime()
        let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
        if (cha < COUNTSECEND) {
          this.sending2 = true
          this.countNum2 = COUNTSECEND - cha
          this.countTimer2 = setInterval(() => {
            this.countNum2 --
            if (this.countNum2 <= 0) {
              clearInterval(this.countTimer2)
              this.countNum2 = COUNTSECEND
              this.sending2 = false
              this.isClickCode2 = false
            }
          }, 1000)
          this.hasSendCode2 = true
        }
      }
    },
    methods: {
      editInfo(type) {
        if (type === 'mobile' && this.userInfo.mobile) {
          return
//           this.$navigateTo({
//           	url: '/pages/mine/agent'
//           })
        } else {
          this.editType = type
          this.realname = ''
          this.idcard = ''
          this.mobile = ''
          this.mobileCode = ''
          this.wx = ''
          this.error = {
            msg: '',
            type: false
          }
          this.editShow = true
        }
      },
      hideEditInfo(cb) {
        this.editShow = false
        if (typeof cb === 'function') cb()
      },
      sendCode() {
        if (this.isClickCode) return
        if (!this.$utils.validatePhone(this.mobile)) {
          this.error = {
            msg: '手机号码格式错误',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: '',
              type: false
            }
          }, 2000)
        } else {
          this.isClickCode = true
          if (!this.hasSendCode) this.hasSendCode = true
          this.error = {
            msg: '验证码已发送至 ' + this.mobile,
            type: false
          }
          this.$http({
            url: this.$apis.phone,
            method: 'POST',
            data: {
              type: 5,
              mobile: this.mobile
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.sending = true
                this.countTimer = setInterval(() => {
                  this.countNum --
                  if (this.countNum <= 0) {
                    clearInterval(this.countTimer)
                    this.countNum = COUNTSECEND
                    this.sending = false
                    this.isClickCode = false
                  }
                }, 1000)
                // 保存发送时间
                uni.setStorageSync('accountCodeStamp', (new Date().getTime()).toString())
              } else {
                this.error = {
                  msg: data.message,
                  type: true
                }
                clearTimeout(this.errorTimer)
                this.errorTimer = setTimeout(() => {
                  this.error = {
                    msg: '',
                    type: false
                  }
                }, 2000)
                this.sending = false
                this.isClickCode = false
              }
            }
          })
        }
      },
      sendCode2() {
        if (this.isClickCode2) return
        this.isClickCode2 = true
        if (!this.hasSendCode2) this.hasSendCode2 = true
        this.error = {
          msg: '验证码已发送至 ' + this.userInfo.mobile,
          type: false
        }
        this.$http({
          url: this.$apis.code,
          method: 'POST',
          data: {
            type: 1,
            mobile: this.userInfo.mobile
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.sending2 = true
              this.countTimer2 = setInterval(() => {
                this.countNum2 --
                if (this.countNum2 <= 0) {
                  clearInterval(this.countTimer2)
                  this.countNum2 = COUNTSECEND
                  this.sending2 = false
                  this.isClickCode2 = false
                }
              }, 1000)
              // 保存发送时间
              uni.setStorageSync('accountCodeStamp2', (new Date().getTime()).toString())
            } else {
              this.error = {
                msg: data.message,
                type: true
              }
              clearTimeout(this.errorTimer)
              this.errorTimer = setTimeout(() => {
                this.error = {
                  msg: '',
                  type: false
                }
              }, 2000)
              this.sending2 = false
              this.isClickCode2 = false
            }
          }
        })
      },
      submit() {
        let _this = this
        if (_this.editing) return false
        let infodata = {
          'real_name': '',
          'id_card': '',
          'verify_code': '',
          'mobile': '',
          'wx_number': '',
          'wx_qrcode_url': '',
          'wxq_qrcode_url': ''
        }
        if (_this.editType === 'real') {
          // 实名
          if (!this.$utils.validateName(_this.realname)) {
            _this.error = {
              msg: '请输入正确的姓名',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else if (!this.$utils.validateIdCard(_this.idcard)) {
            _this.error = {
              msg: '请输入正确的身份证号码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else {
            let realInfodata = {
              'real_name': '',
              'id_card': ''
            }
            realInfodata.real_name = _this.realname
            realInfodata.id_card = _this.idcard
            _this.editing = true
            this.$http({
              url: this.$apis.realname,
              method: 'POST',
              data: realInfodata,
              cb: (err, data) => {
                if (!err && data.success) {
                  _this.hideEditInfo(() => {
                    uni.showModal({
                    	title: '提示',
                      content: '实名成功',
                      showCancel: false,
                      confirmText: '确认',
                      success: res => {
                      	if (res.confirm) {
                          _this.updateInfo()
                        }
                      }
                    })
                  })
                } else {
                  _this.error = {
                    msg: data && data.message ? data.message : '实名失败，请稍后再试',
                    type: true
                  }
                  clearTimeout(_this.errorTimer)
                  _this.errorTimer = setTimeout(() => {
                    _this.error = {
                      msg: '',
                      type: false
                    }
                  }, 2000)
                }
                _this.editing = false
              }
            })
          }
        } else if (_this.editType === 'mobile') {
          // 修改手机
          if (!this.$utils.validatePhone(_this.mobile)) {
            _this.error = {
              msg: '手机号码格式错误',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else if (_this.mobileCode.length <= 0) {
            _this.error = {
              msg: '请输入验证码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else {
            infodata.mobile = _this.mobile
            infodata.verify_code = _this.mobileCode
            _this.editing = true
            this.$http({
              url: this.$apis.updateInfo,
              method: 'PUT',
              data: infodata,
              cb: (err, data) => {
                if (!err && data.success) {
                  _this.hideEditInfo(() => {
                    uni.showModal({
                      title: '提示',
                      content: '保存成功',
                      showCancel: false,
                      confirmText: '确认',
                      success: res => {
                        if (res.confirm) {
                          _this.updateInfo()
                        }
                      }
                    })
                  })
                  uni.removeStorageSync('accountCodeStamp')
                } else {
                  _this.error = {
                    msg: data.message,
                    type: true
                  }
                  clearTimeout(_this.errorTimer)
                  _this.errorTimer = setTimeout(() => {
                    _this.error = {
                      msg: '',
                      type: false
                    }
                  }, 2000)
                }
                _this.editing = false
              }
            })
          }
        } else if (_this.editType === 'wx') {
          // 修改微信
          if (!this.$utils.validateWeixin(_this.wx)) {
            _this.error = {
              msg: '请输入正确的微信号',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else {
            infodata.wx_number = _this.wx
            _this.editing = true
            this.$http({
              url: this.$apis.updateInfo,
              method: 'PUT',
              data: infodata,
              cb: (err, data) => {
                if (!err && data.success) {
                  _this.hideEditInfo(() => {
                    uni.showModal({
                      title: '提示',
                      content: '保存成功',
                      showCancel: false,
                      confirmText: '确认',
                      success: res => {
                        if (res.confirm) {
                          _this.updateInfo()
                        }
                      }
                    })
                  })
                } else {
                  _this.error = {
                    msg: data.message,
                    type: true
                  }
                  clearTimeout(_this.errorTimer)
                  _this.errorTimer = setTimeout(() => {
                    _this.error = {
                      msg: '',
                      type: false
                    }
                  }, 2000)
                }
                _this.editing = false
              }
            })
          }
        } else if (_this.editType === 'password') {
          if (_this.password1.length < 6 || _this.password1.length > 16) {
            _this.error = {
              msg: '请输入6位到16位的密码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else if (_this.password2 !== _this.password1) {
            _this.error = {
              msg: '请再次输入相同的新密码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else if (!_this.hasSendCode2) {
            _this.error = {
              msg: '请点击获取验证码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else if (_this.passwordCode.length <= 0) {
            _this.error = {
              msg: '请输入验证码',
              type: true
            }
            clearTimeout(_this.errorTimer)
            _this.errorTimer = setTimeout(() => {
              _this.error = {
                msg: '',
                type: false
              }
            }, 2000)
          } else {
            this.$http({
              url: this.$apis.resetPassword,
              method: 'POST',
              data: {
                password: this.password1,
                mobile: this.userInfo.mobile,
                verify_code: this.passwordCode,
            		repeat_password: this.password2
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  this.$store.commit('logout')
                  uni.showModal({
                  	title: '修改成功',
                    content: '点击确认重新登录',
                    showCancel: false,
                    success: () => {
                      uni.reLaunch({
                      	url: '/pages/login/index'
                      })
                    }
                  })
                } else {
                  this.code = ''
                  uni.showModal({
                  	content: data.message,
                    showCancel: false
                  })
                }
              }
            })
          }
        }
      },
      bindWx() {
        if (this.userInfo.is_bind_wx) return
        uni.showLoading({
        	title: '获取服务商',
        	mask: true
        });
        uni.getProvider({
        	service: 'oauth',
          success: res => {
          	if (~res.provider.indexOf('weixin')) {
              uni.showLoading({
              	title: '拉起微信授权',
              	mask: true
              });
              uni.login({
              	provider: 'weixin',
              	success: res => {
                  uni.showLoading({
                  	title: '正在获取微信信息',
                  	mask: true
                  })
                  uni.getUserInfo({
                  	provider: 'weixin',
                    success: infoRes => {
                      this.$http({
                        url: this.$apis.wxBind,
                        method: 'POST',
                        data: {
                          userInfo: JSON.stringify(infoRes.userInfo)
                        },
                        cb: (err, data) => {
                          if (!err && data.success) {
                            uni.hideLoading()
                            this.updateInfo()
                          } else {
                            uni.showToast({
                            	title: '绑定失败：' + data.message,
                              icon: 'none'
                            })
                          }
                          uni.hideLoading()
                        }
                      })
                    },
                    fail: err => {
                    	uni.hideLoading()
                    	uni.showToast({
                    		title: '绑定失败：' + JSON.stringify(err),
                    	  icon: 'none'
                    	})
                    }
                  })
                },
              	fail: () => {
                  uni.hideLoading()
                  uni.showToast({
                  	title: '拉起微信失败',
                    icon: 'none'
                  })
                }
              });
            } else {
              uni.hideLoading()
              uni.showToast({
              	title: '拉起微信失败',
                icon: 'none'
              })
            }
          },
          fail: () => {
          	uni.hideLoading()
            uni.showToast({
            	title: '获取服务商失败',
              icon: 'none'
            })
          }
        })
      },
      chooseImg(utype) {
        uni.chooseImage({
        	count: 1,
          sizeType: ['compressed'],
          success: res => {
            if (res.tempFiles[0].size / 1024 / 1024 > 2) {
              uni.showModal({
              	title: '提示',
                content: '请选择小于2Mb的图片',
                showCancel: false,
                confirmText: '知道了'
              })
              return
            }
            uni.showLoading({
            	title: '正在上传',
            	mask: true
            });
            uni.uploadFile({
            	url: this.$apis.uploadPic,
              header: {
                Authorization: this.$store.state.jwt
              },
              fileType: 'image',
              filePath: res.tempFilePaths[0],
              name: 'image',
              formData: {
                type: 'wx_qrcode'
              },
              success: res2 => {
                let resData = JSON.parse(res2.data)
                if (!resData.success) {
                  uni.showToast({
                  	title: '图片上传失败，请重新上传',
                    icon: 'none'
                  })
                  uni.hideLoading()
                  return
                }
                let infodata = {}
                if (utype === 'wxq') {
                  infodata.wxq_qrcode_url = resData.data.key
                } else {
                  infodata.wx_qrcode_url = resData.data.key
                }
                this.$http({
                  url: this.$apis.updateInfo,
                  method: 'PUT',
                  data: infodata,
                  cb: (err, data) => {
                    if (!err && data.success) {
                      this.updateInfo()
                    } else {
                      uni.showToast({
                      	title: data.message,
                        icon: 'none'
                      })
                    }
                    uni.hideLoading()
                  }
                })
              },
              fail: () => {
              	uni.showToast({
              		title: '图片上传失败，请重新上传',
              	  icon: 'none'
              	})
                uni.hideLoading()
              }
            })
          },
          fail: () => {
          	uni.showToast({
          		title: '选择图片失败，请稍候再试',
              icon: 'none'
          	})
          }
        })
      },
      clickRole() {
        if (this.role <= 0) {
          // 临时用户去升级
          this.$navigateTo({
          	url: '/pages/mine/agent'
          })
        } else if (this.role > 0 && this.role <= 3) {
          this.$navigateTo({
          	url: '/pages/index/member'
          })
        }
      },
      gotoDeposit() {
        this.$navigateTo({
        	url: '/pages/mine/deposit'
        })
      },
      gotoReal() {
        if (!this.isReal) {
          this.editInfo('real')
        }
      },
      updateInfo() {
        this.$getInfo()
      },
      logout() {
        uni.showModal({
        	title: '退出确认',
          content: '您确认退出登录吗？',
          success: res => {
          	if (res.confirm) {
              this.$store.commit('logout')
              uni.reLaunch({
              	url: '/pages/login/index'
              })
            }
          }
        })
      }
    }
	}
